import { offerService } from '../_services';
import { router } from '../router';

const state = {
    offer: {},
    offers: []
}

const actions = {
    createOffer({ dispatch, commit }, offer) {
        offerService.createOffer(offer)
            .then(
                offer => {
                    router.push('/home'),
                        setTimeout(() => {
                            // display success message after route change completes
                            commit('offerCreateSuccess', offer);
                            dispatch('alert/success', 'Offer created successfully', { root: true });
                        })
                },
                error => {
                    commit('offerCreateFailure', error);
                    dispatch('alert/error', error, { root: true });
                }
            )
    },
    listOffers({ commit }) {
        offerService.listOffer()
            .then(
                offers => commit('getAllSuccess', offers),
                error => commit('getAllFailure', error)
            );
    }

};

const mutations = {
    getAllSuccess(state, offers) {
        state.offers = offers;
    },
    getAllFailure(state, error) {
       //state.offers = [];
    },
    offerCreateFailure(state, error) {
        //state.offer = error
        //state.offer 
    },
    offerCreateSuccess(state, offer) {
        state.offer = {};
    }
};

export const offer = {
    namespaced: true,
    state,
    actions,
    mutations
};