import { authHeader } from '../_auth';
import { router } from '../router';

export const paymentService = {
    prePayment,
    postPayment
};


const api_url = process.env.VUE_APP_OFFER_API_HOST;

function prePayment(offer_id) {
    const requestOptions = {
        method: 'POST',
        headers: authHeader(),
        body: JSON.stringify({"offer_id": offer_id})
    };

    return fetch(`${api_url}ig-offer-portal/api/payment/`, requestOptions).then(handleResponse);
}

function postPayment(payment_response) {
    // Only triggers when success
    const requestOptions = {
        method: 'POST',
        headers: authHeader(),
        body: JSON.stringify(payment_response)
    };

    return fetch(`${api_url}ig-offer-portal/api/payment-success/`, requestOptions).then(handleResponse);
}

function handleResponse(response) {
    return response.text().then(text => {
        const data = text && JSON.parse(text);
        if (!response.ok) {
            console.log(response.status)
            if (response.status === 401 || response.status === 403) {
                // auto logout if 401 response returned from api
                router.push('login');
            }

            const error = (data) || response.statusText;
            return Promise.reject(error);
        }

        return data;
    });
}