import { paymentService } from '../_services';
import { router } from '../router';

const state = {
    payment: {},
    summary: {}
}

const actions = {
    prePayment({ commit }, { offer_id }) {
        return new Promise((resolve, reject) => {
        paymentService.prePayment(offer_id)
            .then(
                summary => { 
                    commit('summarySuccess', summary)
                    resolve()
                },
                error => commit('summaryFailure', error)
            );
        })
    },
    postPayment({ dispatch, commit }, payment_obj) {
        paymentService.postPayment(payment_obj)
            .then(
                resp => {
                    commit('paymentSuccess', resp);
                    router.push('/home');
                    setTimeout(() => {
                        // display success message after route change completes
                        dispatch('alert/success', 'Payment successful', { root: true });
                    })
                },
                error => commit('paymentFailure', error)
            );
    },

};


const mutations = {
    paymentFailure(state, error) {
        //state.offer = error
        //state.offer 
    },
    paymentSuccess(state, offer) {
        state.payment = {};
    },
    summaryFailure(state, error) {
        //state.offer = error
        //state.offer 
    },
    summarySuccess(state, summary) {
        state.summary = summary;
    }
};

export const payment = {
    namespaced: true,
    state,
    actions,
    mutations
};