// Local storage kry names
const TOKEN_KEY = "user_atkn"
const USER_KEY = "user"

const api_url = process.env.VUE_APP_IAM_API_HOST;

// Need to set serverside cors hearders
// response["Access-Control-Allow-Origin"] = "*"
// response["Access-Control-Allow-Headers"] = "content-type"


function login(username, password) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json'},
        body: JSON.stringify({ username, password })
    };

    return fetch(`${api_url}ig-iam/api/get-token/`, requestOptions)
        .then(handleResponse)
        .then(user => {
            // login successful if there's a jwt token in the response
            if (user.token) {
                // store user details and jwt token in local storage to keep user logged in between page refreshes
                //localStorage.setItem('user', JSON.stringify(user));
                localStorage.setItem(TOKEN_KEY, JSON.stringify(user.token));
                localStorage.setItem(USER_KEY, JSON.stringify(username));
            }

            return username;
        });
}

function logout() {
    // remove user from local storage to log user out
    localStorage.removeItem(TOKEN_KEY);
    localStorage.removeItem(USER_KEY);
}

function register(user) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(user)
    };

    return fetch(`${api_url}/users/register`, requestOptions).then(handleResponse);
}

